@mixin style-filter-padding($top, $right, $bottom, $left) {
  width: 100%;
  height: 4.5rem;
  border: 1px solid var(--filter-border-color);
  border-radius: 8px;
  font-size: 1.3rem;
  color: var(--text-color);
  background-color: var(--color-grey-1);

  padding: $top $right $bottom $left;
}
